html {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  --link-color: #00acdc;
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo__image {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--link-color);
}

.is-active:after {
  content: "";
  border-bottom: 4px solid var(--link-color);
  border-radius: 2px;
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 0;
}

